










































































































































































































































































































































































































html {
  height: 100%;
}
body {
  height: 100%;
  font: 100% "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
a:hover,
a:focus {
  text-decoration: none;
}
#app {
  width: 100%;
}
.loading-gif {
  margin-top: 60px;
  margin-bottom: 60px;
  text-align: center;
  clear: both;
}
.loading-gif img {
  display: inline-block;
  width: 20px;
}
.course-filter-wrapper {
  position: relative;
  z-index: 999;
}
.course-filter-wrapper a {
  -webkit-tap-highlight-color:rgba(0,0,0,0)
}
.course-filter-header div {
  float: left;
  width: 25%;
}
.course-filter-header a {
  display: block;
  background-color: #f9f9f9;
  width: 100%;
  height: 45px;
  line-height: 45px;
  text-align: center;
  color: #000;
  font-size: 16px;
  vertical-align: top;
}
.course-filter-header a.active {
  color: #ffad00;
}
.course-caret {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 15px;
  vertical-align: middle;
  border-top: 6px dashed;
  border-top: 6px solid \9;
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
  color: #ccc;
}
.course-filter-header a.active .course-caret {
  color: #ffad00;
  border-top: 0px;
  border-bottom: 6px dashed;
  border-bottom: 6px solid \9;
}
.course-filter-body {
  position: absolute;
  left: 0;
  right: 0;
  top: 45px;
  max-height: 200px;
  overflow-y: scroll;
  background-color: #fff;
}
.course-filter-inner {
  float: left;
  width: 50%;
}
.course-filter-body-item a {
  display: block;
  height: 50px;
  line-height: 50px;
  color: #333;
  padding-left: 10%;
  font-size: 14px;
}
.course-filter-body-item a.active {
  color: #ffae00;
}
.course-items-wrapper {
  padding: 20px 15px 30px;
  background-image: url(./assets/backgd.png);
  background-repeat: repeat;
}
.course-item {
  margin-bottom: 20px;
}
.course-item-wrapper {
  background-color: #fff;
  padding: 10px;
}
.course-item-inner {
  display: block;
  width: 100%;
  position: relative;
}
.course-item-inner img {
  width: 100%;
  height: auto;
}
.course-item-title {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 30px;
  line-height: 30px;
  font-size: 16px;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  text-align: center;
  letter-spacing: 2px;
}
.course-item-text {
  padding: 10px 0 6px;
  font-size: 14px;
  line-height: 150%;
  color: #666;
  border-bottom: 1px solid #ccc;
  text-align: justify;
}
.course-item-icons {
  padding-top: 8px;
  font-size: 12px;
  color: #ffae00;
}
.course-item-sub-icons1 {
  float: right;
}
.course-item-sub-icons2 {
  float: left;
}
.course-item-sub-icons1 .course-icon {
  float: left;
}
.course-item-sub-icons2 .course-icon {
  float: left;
  margin-right: 10px;
}
.course-item-sub-icons1 .course-icon img,
.course-item-sub-icons2 .course-icon img {
  margin-right: 3px;
  vertical-align: middle;
}
.course-item-sub-icons1 .course-icon span,
.course-item-sub-icons2 .course-icon span {
  vertical-align: middle;
}
.course-loca-icon {
  display: none;
}
.course-time-icon img {
  width: 20px;
  height: auto;
}
.course-like-icon img {
  width: 20px;
  height: auto;
}
.course-praise-icon img {
  width: 20px;
  height: auto;
}
.course-view-icon img {
  width: 20px;
  height: auto;
}
.p-course-filter-wrapper {
  display: none;
}
.get-more-item {
  clear: both;
  margin-top: 40px;
}
.get-more-item .white-line {
  display: block;
  width: 100%;
  border-top: 1px solid #fff;
}
.get-more-item a {
  margin-top: -15px;
  display: block;
  width: 30%;
  margin-left: auto;
  margin-right: auto;
  color: #ffae00;
  text-align: center;
  font-size: 16px;
  height: 30px;
  line-height: 30px;
  background-image: url(./assets/backgd.png);
  background-repeat: repeat;
}
.get-more-item img {
  display: inline-block;
  margin-left: 15px;
  width: 25px;
}
@media (min-width: 768px) {
  .course-filter-wrapper {
    display: none;
  }
  .course-items-wrapper {
    padding-top: 60px;
    padding-left: 5%;
    padding-right: 5%;
  }
  /* pc端筛选样式 */
  .p-course-filter-wrapper {
    display: block;
    background-color: #f9f9f9;
    padding: 60px 2%;
  }
  .p-course-inner-filter {
    width: 100%;
    background-color: #fff;
    border: 1px solid #ccc;
    padding: 25px;
  }
  .p-single-filter {
    margin-bottom: 20px;
  }
  .p-single-filter:last-child {
    margin-bottom: 0;
  }
  .p-single-filter-title {
    float: left;
    color: #333;
    font-size: 16px;
    height: 22px;
    line-height: 22px;
    padding-right: 20px;
    border-right: 1px solid #ccc;
  }
  .p-single-filter-body {
    margin-left: 80px;
    font-size: 16px;
  }
  .p-single-filter-item {
    float: left;
    position: relative;
    margin-bottom: 10px;
  }
  .p-single-filter-item.big-margin {
    margin-bottom: 55px;
  }
  .p-single-filter-item a {
    display: inline-block;
    margin-right: 25px;
    padding: 2px 15px;
    font-size: 16px;
    height: 22px;
    line-height: 18px;
    color: #666;
  }
  .p-single-filter-item a.active {
    color: #ffae00;
  }
  .p-single-filter-item a:hover {
    color: #fff;
    background-color: #ffae00;
  }
  .p-single-filter-item .icon {
    position: absolute;
    display: block;
    z-index: 999;
    background-image: url(./assets/arrow.png);
    background-color: #fff;
    background-size: contain;
    background-repeat: no-repeat;
    top: 30px;
    width: 15px;
    height: 8px;
    left: 30px;
    z-index: 100;
  }
  .p-single-filter-sub-items {
    position: absolute;
    z-index: 99;
    top: 36px;
    left: -75px;
    width: 315px;
    border: 1px solid #ffae00;
    padding: 10px 5px;
  }
  .p-single-filter-sub-items a {
    float: left;
  }
  .p-single-filter-sub-items a:last-child {
    margin: 0;
  }
}

@media (min-width: 992px) {
  .loading-gif img {
    width: 30px;
  }
  .p-course-filter-wrapper {
    padding: 60px 3%;
  }
  .course-items-wrapper {
    padding-top: 60px;
    padding-left: 3%;
    padding-right: 3%;
  }
  .course-item {
    float: left;
    width: 50%;
    margin-bottom: 50px;
  }
  .course-item:nth-child(odd) .course-item-wrapper {
    margin-right: 25px;
  }
  .course-item:nth-child(even) .course-item-wrapper {
    margin-left: 25px;
  }
  .course-item-sub-icons1 {
    float: left;
  }
  .course-item-sub-icons1 .course-icon {
    margin-right: 10px;
  }
  .course-item-sub-icons2 {
    float: right;
  }
  .course-loca-icon {
    display: block;
  }
  .course-loca-icon img {
    width: 17px;
    height: auto;
  }
  .get-more-item a {
    margin-top: -15px;
    width: 25%;
    font-size: 20px;
    height: 30px;
    line-height: 30px;
  }
  .get-more-item img {
    display: inline-block;
    margin-left: 15px;
    width: 35px;
  }
}

@media (min-width: 1200px) {
  .p-course-filter-wrapper {
    padding: 60px 10%;
  }
  .course-items-wrapper {
    padding-left: 12%;
    padding-right: 12%;
  }
  .course-item {
    margin-bottom: 60px;
  }
  .course-item:nth-child(odd) .course-item-wrapper {
    margin-right: 30px;
  }
  .course-item:nth-child(even) .course-item-wrapper {
    margin-left: 30px;
  }
}
@media (min-width: 1600px) {
  .p-course-filter-wrapper {
    padding: 60px 14%;
  }
  .course-items-wrapper {
    padding-left: 16%;
    padding-right: 16%;
  }
  .course-item {
    margin-bottom: 80px;
  }
  .course-item:nth-child(odd) .course-item-wrapper {
    margin-right: 40px;
  }
  .course-item:nth-child(even) .course-item-wrapper {
    margin-left: 40px;
  }
}



